"use client"

import { Button } from "@/components/ui/button"
import { useState, useEffect } from "react"
import Link from "next/link"
import { useLanguage } from "@/contexts/language-context"
import { motion } from "framer-motion"
import { FaWallet, FaServer, FaSignInAlt, FaUserPlus, FaThLarge } from "react-icons/fa"

export function Header() {
  const { t, language, setLanguage } = useLanguage()
  const [isAuthenticated, setIsAuthenticated] = useState(false)
  const [balance, setBalance] = useState<number | null>(null)
  const [serversCount, setServersCount] = useState<number>(0)

  useEffect(() => {
    const checkAuth = async () => {
      try {
        const response = await fetch("/api/user", { cache: "no-store" })
        setIsAuthenticated(response.ok)
        if (response.ok) {
          const [profileRes, ordersRes] = await Promise.all([
            fetch("/api/profile", { cache: "no-store" }),
            fetch("/api/orders", { cache: "no-store" }),
          ])
          if (profileRes.ok) {
            const p = await profileRes.json()
            setBalance(typeof p?.balance === "number" ? p.balance : 0)
          } else {
            setBalance(0)
          }
          if (ordersRes.ok) {
            const o = await ordersRes.json()
            setServersCount(Array.isArray(o) ? o.length : 0)
          }
        } else {
          setBalance(null)
          setServersCount(0)
        }
      } catch {
        setIsAuthenticated(false)
      }
    }
    checkAuth()
  }, [])

  return (
    <motion.header
      className="border-b-2 border-border/30 backdrop-blur-lg bg-background/80 sticky top-0 z-50 shadow-md"
      initial={{ y: -40, opacity: 0 }}
      animate={{ y: 0, opacity: 1 }}
      transition={{ duration: 0.4, ease: "easeOut" }}
    >
      <div className="relative container mx-auto px-4 sm:px-6 h-16 flex items-center justify-between">
        {/* Логотип строго в левом углу: absolute left-0, без margin/padding */}
        <div className="absolute left-0 top-1/2 -translate-y-1/2 flex items-center z-50">
          <Link href="/" className="flex items-center">
            <motion.div
              initial={{ scale: 0.95, rotate: -5, opacity: 0.9 }}
              animate={{ 
                scale: [0.95, 1.05, 0.98, 1], 
                rotate: [-5, 3, -1, 0], 
                opacity: 1,
                filter: [
                  "brightness(0.98) drop-shadow(0 0 2px #8c6cfa55)",
                  "brightness(1.08) drop-shadow(0 0 12px #ac94fbcc)",
                  "brightness(1.04) drop-shadow(0 0 8px #8c6cfa55)",
                  "brightness(1.06) drop-shadow(0 0 18px #8c6cfa66)"
                ],
              }}
              transition={{
                duration: 0.9,
                ease: "easeInOut",
              }}
              whileHover={{
                scale: 1.05,
                rotate: 0.5,
                filter: "brightness(1.1) drop-shadow(0 0 16px #8c6cfacc)",
                transition: { duration: 0.3, ease: "easeOut" }
              }}
              className="outline-none"
            >
              <svg
                width={44}
                height={44}
                viewBox="0 0 44 44"
                fill="none"
                xmlns="http://www.w3.org/2000/svg"
                className="block"
                style={{ filter: "drop-shadow(0 0 18px #8c6cfa33)" }}
              >
                <radialGradient id="logo-glow" cx="50%" cy="50%" r="50%">
                  <stop offset="0%" stopColor="#8c6cfa" stopOpacity="0.25"/>
                  <stop offset="100%" stopColor="#fff" stopOpacity="0"/>
                </radialGradient>
                <circle cx="22" cy="22" r="21" fill="url(#logo-glow)" />
                <circle cx="22" cy="22" r="17" fill="#18103a" stroke="#8c6cfa" strokeWidth="2.6"/>
                <text
                  x="50%" y="56%"
                  textAnchor="middle"
                  fill="#8c6cfa"
                  fontSize="16"
                  fontWeight="bold"
                  fontFamily="inherit"
                  letterSpacing="2"
                  filter="url(#svgshadow)"
                  style={{
                    textShadow: "0 0 10px #8c6cfa44, 0 1px 0 black"
                  }}
                  dominantBaseline="middle"
                >VDS</text>
              </svg>
            </motion.div>
          </Link>
        </div>

        {/* Placeholder без padding слева, с отступом ml-[44px] для избежания перекрытия */}
        <div className="flex-1 flex ml-[44px]">
          <Link href="/" className="flex items-center gap-3 group select-none">
            <motion.span
              initial={{ opacity: 0.6, x: -5 }}
              animate={{ opacity: 1, x: 0 }}
              transition={{ delay: 0.3, duration: 0.5, type: "spring" }}
              className="hidden md:inline-block font-black tracking-tight text-xl text-primary group-hover:text-accent transition-colors duration-300 ease-in-out"
            >
              VDS Hosting
            </motion.span>
          </Link>
        </div>

        <motion.div
          className="flex items-center gap-4 sm:gap-5"
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          transition={{ duration: 0.35, delay: 0.15 }}
        >
          <Button
            variant="outline"
            size="sm"
            className="hidden sm:inline-flex rounded-full px-4 py-2 border font-semibold border-primary/40 bg-primary/5 hover:bg-primary/15 hover:shadow-md transition-all duration-300 ease-out text-sm"
            onClick={() => setLanguage(language === "ru" ? "en" : "ru")}
          >
            {language === "ru" ? "RU" : "EN"}
          </Button>
          {isAuthenticated && (
            <div className="hidden sm:flex items-center gap-3">
              <motion.div
                className="flex items-center gap-2 px-3 py-1.5 rounded-full bg-gradient-to-r from-primary/15 to-accent/15 border shadow-sm border-primary/25 text-base font-semibold hover:bg-gradient-to-r hover:from-primary/25 hover:to-accent/25 transition-all duration-300"
                whileHover={{ scale: 1.03, boxShadow: "0 0 8px #8c6cfa55" }}
                transition={{ duration: 0.3, ease: "easeOut" }}
              >
                <FaWallet className="text-primary" size={17} />
                <span className="text-muted-foreground">{language === "ru" ? "Баланс" : "Balance"}</span>
                <span className="font-bold text-foreground">
                  {new Intl.NumberFormat("en-US", { style: "currency", currency: "USD" }).format(balance ?? 0)}
                </span>
              </motion.div>
              <motion.div
                className="flex items-center gap-2 px-3 py-1.5 rounded-full bg-secondary/30 border shadow-sm border-secondary/25 text-base text-foreground font-semibold hover:bg-secondary/40 transition-all duration-300"
                whileHover={{ scale: 1.03, boxShadow: "0 0 8px #8c6cfa55" }}
                transition={{ duration: 0.3, ease: "easeOut" }}
              >
                <FaServer /> {language === "ru" ? "VPS" : "VPS"}: <span className="font-bold ml-1">{serversCount}</span>
              </motion.div>
            </div>
          )}
          {isAuthenticated ? (
            <motion.div whileHover={{ scale: 1.03, boxShadow: "0 0 12px #8c6cfa77" }} transition={{ duration: 0.3, ease: "easeOut" }}>
              <Link href="/dashboard">
                <Button
                  size="sm"
                  className="rounded-full px-5 py-2 bg-primary hover:bg-primary/95 text-white font-extrabold shadow-md hover:shadow-lg text-base transition-all duration-300 border border-primary/70 flex gap-2 items-center"
                >
                  <FaThLarge size={17} /> {t("header.dashboard")}
                </Button>
              </Link>
            </motion.div>
          ) : (
            <>
              <motion.div whileHover={{ scale: 1.03, boxShadow: "0 0 8px #8c6cfa55" }} transition={{ duration: 0.3, ease: "easeOut" }}>
                <Link href="/login">
                  <Button
                    variant="outline"
                    size="sm"
                    className="rounded-full px-4 py-2 font-bold border border-primary/25 bg-transparent hover:bg-secondary/5 hover:shadow-md transition-all duration-300 text-sm flex gap-2 items-center"
                  >
                    <FaSignInAlt size={16} /> {t("header.login")}
                  </Button>
                </Link>
              </motion.div>
              <motion.div whileHover={{ scale: 1.03, boxShadow: "0 0 12px #8c6cfa77" }} transition={{ duration: 0.3, ease: "easeOut" }}>
                <Link href="/register">
                  <Button
                    size="sm"
                    className="rounded-full px-5 py-2 bg-primary hover:bg-primary/95 text-white font-extrabold border border-primary/70 shadow-md hover:shadow-lg text-base transition-all duration-300 flex gap-2 items-center"
                  >
                    <FaUserPlus size={16} /> {t("header.register")}
                  </Button>
                </Link>
              </motion.div>
            </>
          )}
        </motion.div>
      </div>
    </motion.header>
  )
}